<template>
    <div class="input-field-wrapper" :class="computedClass">
        <div class="input-field-group">
            <div class="text-input">
                <input
                    :id="id"
                    :type="type"
                    :placeholder="placeholder"
                    :value="value"
                    autocomplete="off"
                    @input="inputChanged"
                    @keyup="$emit('keyup', $event)"
                    :maxlength="maxLength"
                    required
                />
                <label :for="id">{{placeholder}}</label>
            </div>
        </div>
    </div>
</template>

<script>
    import { uniqueId } from 'lodash';

    export default {
        name: 'DefaultInputField',
        props: {
            placeholder: {
                type: String,
                default: '',
            },
            type: {
                type: String,
                default: 'text',
            },
            value: {
                type: String,
                required: true,
            },
            invalid: {
                type: Boolean,
                default: false,
            },
            maxLength: {
                type: Number,
                default: undefined,
            }
        },
        data() {
            return {
                id: null,
            };
        },
        computed: {
            computedClass() {
                return {
                    invalid: this.invalid,
                };
            },
        },
        mounted() {
            this.id = uniqueId('input-field-group-');
        },
        methods: {
            inputChanged(event) {
                this.$emit('input', event.target.value);
            },
        },
    };
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_variables.scss";
.input-field-wrapper {
    margin-bottom: 16px;

    .input-field-group {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
    }

    /** Input styling **/
    .text-input {
        position: relative;
        display: block;
        height: 56px;
        width: 100%;
    }

    .text-input > input {
        position: absolute;
        bottom: 0;
        display: block;
        height: 40px;
        width: 100%;
        line-height: 40px;
        border: 0;
        border-bottom: 1px solid $grey-light;
        border-radius: 0;
        outline: 0;
        background: transparent;
        font-size: 14px;
        color: $grey-light;
        /*   transition: all 1s ease-in-out; */
    }

    .text-input > input::placeholder {
        color: transparent;
    }

    .text-input > label {
        position: absolute;
        top: 25px;
        left: 9px;
        color: $grey-light;
        cursor: text;
        font-size: 16px;
        transition: all 0.2s ease-in-out;
        user-select: none;
    }

    .text-input > input:focus,
    .text-input > input:not(:placeholder-shown) {
        border-bottom: 2px solid $primary;
    }

    .text-input > input:focus + label,
    .text-input > input:not(:placeholder-shown) + label {
        top: 0;
        left: 0;
        font-size: 14px;
        color: $primary;
    }

    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    input:-webkit-autofill:active {
        -webkit-transition-delay: 9999s;
        -webkit-transition: color 9999s ease-out, background-color 9999s ease-out;
    }

    &.invalid {
        .text-input > input {
            border-bottom-color: $red;
        }

        .text-input > input + label {
            color: $red;
        }
    }
}
</style>
