<template>
    <div class="ProperPage">
        <MainPageHeader/>
        <div v-if="tab" class="content">
            <header class="main-header">
                <p>Lorem ips</p>
                <h1>{{tab.name}}</h1>
            </header>
            <div class="divider" />
            <div class="text-header">
                <h1>Lorem ipsum sed.</h1>
                <p>Lorem ipsum sed. Lorem</p>
                <h2>Lorem ipsum sed. Lorem ipsum sed.Lorem ipsum sed.</h2>
            </div>
            <ProperSection class="section-0"
                :visible="chosenSection === null || chosenSection === 0"
                :chosen="chosenSection === 0"
                :active="activeSection == 0"
                title="Lorem"
                subtitle="Lorem ipsum sed."
                contentHeight="395px"
                :hasIcon="true"
                @click="openSection(0)">
                <CardDetailsForm @submit="chooseSection(0)" />
            </ProperSection>
            <ProperSection class="section-1"
                :visible="chosenSection === null || chosenSection === 1"
                :chosen="chosenSection === 1"
                :active="activeSection == 1"
                contentHeight="230px"
                title="Lorem ipsum sed."
                @click="openSection(1)">
                <form class="fields" @submit="handleSection1Submit">
                    <DefaultInputField type="text" placeholder="Pole_1" v-model="field5" />
                    <BigButton class="big-button" borderColor="primary" type="submit">Button</BigButton>
                </form>
            </ProperSection>
            <ProperSection class="section-2"
                :visible="chosenSection === null || chosenSection === 2"
                :chosen="chosenSection === 2"
                :active="activeSection == 2"
                title="Lorem ipsum sed."
                contentHeight="65px"
                @click="openSection(2)">
            </ProperSection>
        </div>
        <TabFooter v-if="tab"
            :tab="tab"
            :buttonEnabled="chosenSection !== null || activeSection === 2"
            @buttonClicked="proceed" />
        <PageNotFound v-if="loadError" :message="loadError" />
        <ContentLoader v-if="!tab" />
    </div>
</template>

<script>
    import MainPageHeader from '../components/MainPageHeader.vue';
    import PageNotFound from '../components/PageNotFound.vue';
    import ContentLoader from '../components/ContentLoader.vue';
    import TabFooter from '../components/TabFooter.vue';
    import DefaultInputField from '../components/input/DefaultInputField.vue';
    import BigButton from '../components/input/BigButton.vue';
    import CardDetailsForm from '../components/CardDetailsForm.vue';
    import ProperSection from '../components/proper/ProperSection.vue';
    import { getTabService } from '../services/tabService';

    export default {
        name: 'ProperPage',
        components: {
            MainPageHeader,
            PageNotFound,
            ContentLoader,
            TabFooter,
            DefaultInputField,
            BigButton,
            CardDetailsForm,
            ProperSection,
        },

        data() {
            return {
                tab: null,
                loadError: null,
                activeSection: 0,
                chosenSection: null,
                field5: '',
            };
        },

        mounted() {
            (async () => {
                this.tab = await getTabService().getTab(this.$route.params.tabKey);

                if (this.tab === null) {
                    this.loadError = `The tab with the specified key doesn't exist`;
                }
            })();
        },

        methods: {
            acceptAllInput() {
                if (this.policyStates.some(p => p == false)) {
                    this.policyStates = this.policyStates.map(p => true);
                } else {
                    this.policyStates = this.policyStates.map(p => false);
                }
            },

            policyInput(index, state) {
                const states = this.policyStates.slice();
                states[index] = !this.policyStates[index];
                this.policyStates = states;
            },

            openSection(index) {
                this.activeSection = index;
            },

            proceed() {
                this.tab = null;
            },

            chooseSection(index) {
                this.chosenSection = index;
            },

            handleSection1Submit(e) {
                e.preventDefault();
                this.chooseSection(1);
            }
        },
    };
</script>

